import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function shareholdersEquityPerShareTtm(tableName: string) {
  const title = "Equity/Share (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["shareholdersEquityPerShareTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Total equity per share.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about equity per share?`,
              value:
                info.row.original.financialData.companyRatios?.data?.shareholdersEquityPerShareTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.shareholdersEquityPerShareTtm}
        />
      )
    },
  })
}

export { shareholdersEquityPerShareTtm }
